import { Edit2, Plus, Trash2 } from 'lucide-react';
import React, { useState } from 'react';

interface CalendarEvent {
  id: string;
  title: string;
  start: Date;
  end: Date;
  description?: string;
  type: 'meeting' | 'task' | 'reminder' | 'other';
  color: string;
}

const eventTypes = [
  { label: '会议', value: 'meeting', color: '#3b82f6' },
  { label: '任务', value: 'task', color: '#10b981' },
  { label: '提醒', value: 'reminder', color: '#f59e0b' },
  { label: '其他', value: 'other', color: '#6b7280' },
];

const CalendarManagement: React.FC = () => {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [events, setEvents] = useState<CalendarEvent[]>([
    {
      id: '1',
      title: '团队周会',
      start: new Date(2025, 9, 13, 10),
      end: new Date(2025, 9, 13, 11),
      type: 'meeting',
      color: '#3b82f6',
      description: '讨论本周工作进展',
    },
    {
      id: '2',
      title: '项目截止日',
      start: new Date(2025, 9, 15, 9),
      end: new Date(2025, 9, 15, 18),
      type: 'task',
      color: '#10b981',
      description: '完成项目第一阶段开发',
    },
  ]);

  const [showModal, setShowModal] = useState(false);
  const [editingEvent, setEditingEvent] = useState<CalendarEvent | null>(null);
  const [formData, setFormData] = useState({
    title: '',
    startDate: '',
    startTime: '',
    endDate: '',
    endTime: '',
    description: '',
    type: 'other' as CalendarEvent['type'],
  });

  const [viewMode, setViewMode] = useState<'month' | 'day'>('month');
  const [showMeetings, setShowMeetings] = useState(true);
  const [showTasks, setShowTasks] = useState(true);
  const [showWeekends, setShowWeekends] = useState(true);

  const formatTime = (date: Date) =>
    date.toLocaleTimeString('zh-CN', { hour: '2-digit', minute: '2-digit' });
  const handlePrevMonth = () =>
    setCurrentDate(
      new Date(currentDate.getFullYear(), currentDate.getMonth() - 1),
    );
  const handleNextMonth = () =>
    setCurrentDate(
      new Date(currentDate.getFullYear(), currentDate.getMonth() + 1),
    );
  const handleToday = () => {
    const today = new Date();
    setCurrentDate(today);
    setSelectedDate(today);
  };

  const getDaysInMonth = (date: Date) => {
    const year = date.getFullYear();
    const month = date.getMonth();
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);
    const days: (Date | null)[] = [];

    for (let i = 0; i < firstDay.getDay(); i++) days.push(null);
    for (let i = 1; i <= lastDay.getDate(); i++)
      days.push(new Date(year, month, i));
    while (days.length % 7 !== 0) days.push(null);

    return days;
  };

  const getEventsForDate = (date: Date | null) => {
    if (!date) return [];
    let filteredEvents = events.filter(
      (event) => event.start.toDateString() === date.toDateString(),
    );

    if (!showMeetings) {
      filteredEvents = filteredEvents.filter((e) => e.type !== 'meeting');
    }
    if (!showTasks) {
      filteredEvents = filteredEvents.filter((e) => e.type !== 'task');
    }

    return filteredEvents;
  };

  const openCreateModal = () => {
    setEditingEvent(null);
    const now = selectedDate;
    setFormData({
      title: '',
      startDate: now.toISOString().split('T')[0],
      startTime: '09:00',
      endDate: now.toISOString().split('T')[0],
      endTime: '10:00',
      description: '',
      type: 'other',
    });
    setShowModal(true);
  };

  const openEditModal = (event: CalendarEvent) => {
    setEditingEvent(event);
    setFormData({
      title: event.title,
      startDate: event.start.toISOString().split('T')[0],
      startTime: formatTime(event.start),
      endDate: event.end.toISOString().split('T')[0],
      endTime: formatTime(event.end),
      description: event.description || '',
      type: event.type,
    });
    setShowModal(true);
  };

  const handleSave = () => {
    if (!formData.title.trim()) return alert('请输入事件标题');
    const start = new Date(`${formData.startDate}T${formData.startTime}`);
    const end = new Date(`${formData.endDate}T${formData.endTime}`);
    if (end <= start) return alert('结束时间必须晚于开始时间');

    const color =
      eventTypes.find((t) => t.value === formData.type)?.color || '#6b7280';

    if (editingEvent) {
      setEvents(
        events.map((e) =>
          e.id === editingEvent.id
            ? {
                ...e,
                title: formData.title,
                start,
                end,
                description: formData.description,
                type: formData.type,
                color,
              }
            : e,
        ),
      );
    } else {
      setEvents([
        ...events,
        {
          id: Date.now().toString(),
          title: formData.title,
          start,
          end,
          description: formData.description,
          type: formData.type,
          color,
        },
      ]);
    }
    setShowModal(false);
  };

  const handleDelete = (id: string) => {
    if (confirm('确定删除吗？')) setEvents(events.filter((e) => e.id !== id));
  };

  const renderCalendarGrid = (
    days: (Date | null)[],
    isSmall: boolean = false,
  ) => {
    let filteredDays = days;
    let weekdays = ['日', '一', '二', '三', '四', '五', '六'];

    if (!showWeekends) {
      filteredDays = [];
      weekdays = ['一', '二', '三', '四', '五'];
      for (let i = 0; i < days.length; i++) {
        const weekdayIdx = i % 7;
        if (weekdayIdx !== 0 && weekdayIdx !== 6) {
          filteredDays.push(days[i]);
        }
      }
    }

    return (
      <div
        style={{
          display: 'grid',
          gridTemplateColumns: `repeat(${weekdays.length}, 1fr)`,
          gap: '4px',
        }}
      >
        {weekdays.map((d) => (
          <div
            key={d}
            style={{
              textAlign: 'center',
              fontWeight: '600',
              fontSize: isSmall ? '11px' : '14px',
              padding: isSmall ? '4px 0' : '8px 0',
              color: '#4b5563',
            }}
          >
            {d}
          </div>
        ))}
        {filteredDays.map((day, idx) => {
          const dayEvents = day ? getEventsForDate(day) : [];
          const isToday =
            day && day.toDateString() === new Date().toDateString();
          const isSelected =
            day && day.toDateString() === selectedDate.toDateString();

          return (
            <div
              key={day ? day.toISOString() : `empty-${idx}`}
              onClick={() => day && setSelectedDate(day)}
              style={{
                minHeight: isSmall ? '28px' : '112px',
                border: '1px solid',
                borderColor: !day
                  ? '#e5e7eb'
                  : isSelected
                    ? '#60a5fa'
                    : isToday
                      ? '#fbbf24'
                      : '#e5e7eb',
                borderRadius: isSmall ? '4px' : '6px',
                padding: isSmall ? '2px' : '8px',
                cursor: 'pointer',
                display: 'flex',
                flexDirection: 'column',
                backgroundColor: !day
                  ? '#f9fafb'
                  : isSelected
                    ? '#eff6ff'
                    : isToday
                      ? '#fef3c7'
                      : '#ffffff',
              }}
              onMouseEnter={(e) => {
                if (day && !isSelected && !isToday) {
                  e.currentTarget.style.backgroundColor = '#f9fafb';
                }
              }}
              onMouseLeave={(e) => {
                if (day && !isSelected && !isToday) {
                  e.currentTarget.style.backgroundColor = '#ffffff';
                }
              }}
            >
              {day && (
                <>
                  <div
                    style={{
                      fontSize: isSmall ? '11px' : '14px',
                      fontWeight: '600',
                      marginBottom: '4px',
                      color: isToday
                        ? '#b45309'
                        : isSelected
                          ? '#1d4ed8'
                          : '#374151',
                    }}
                  >
                    {day.getDate()}
                  </div>
                  {!isSmall && (
                    <div
                      style={{
                        flex: 1,
                        display: 'flex',
                        flexDirection: 'column',
                        gap: '4px',
                        overflow: 'hidden',
                      }}
                    >
                      {dayEvents.map((ev) => (
                        <div
                          key={ev.id}
                          style={{
                            backgroundColor: ev.color,
                            color: 'white',
                            padding: '4px 8px',
                            borderRadius: '4px',
                            fontSize: '12px',
                            cursor: 'pointer',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between',
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',
                            whiteSpace: 'nowrap',
                            transition: 'box-shadow 0.2s',
                          }}
                          onClick={(e) => {
                            e.stopPropagation();
                            openEditModal(ev);
                          }}
                          onMouseEnter={(e) => {
                            e.currentTarget.style.boxShadow =
                              '0 4px 6px -1px rgba(0, 0, 0, 0.1)';
                          }}
                          onMouseLeave={(e) => {
                            e.currentTarget.style.boxShadow = 'none';
                          }}
                        >
                          <span
                            style={{
                              overflow: 'hidden',
                              textOverflow: 'ellipsis',
                              flex: 1,
                            }}
                          >
                            {ev.title}
                          </span>
                          <span
                            style={{
                              fontSize: '10px',
                              opacity: 0.9,
                              marginLeft: '4px',
                              whiteSpace: 'nowrap',
                            }}
                          >
                            {formatTime(ev.start)}
                          </span>
                        </div>
                      ))}
                    </div>
                  )}
                </>
              )}
            </div>
          );
        })}
      </div>
    );
  };

  const days = getDaysInMonth(currentDate);
  const monthName = currentDate.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: 'long',
  });

  return (
    <div
      style={{
        minHeight: '100vh',
        backgroundColor: '#f9fafb',
        padding: '24px',
      }}
    >
      <div style={{ maxWidth: '1280px', margin: '0 auto' }}>
        <div style={{ display: 'flex', gap: '24px', flexDirection: 'row' }}>
          {/* 左侧小日历 */}
          <div
            style={{
              width: '280px',
              display: 'flex',
              flexDirection: 'column',
              gap: '12px',
              flexShrink: 0,
            }}
          >
            <div
              style={{
                backgroundColor: 'white',
                borderRadius: '12px',
                boxShadow: '0 1px 3px 0 rgba(0, 0, 0, 0.1)',
                padding: '12px',
              }}
            >
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  marginBottom: '8px',
                }}
              >
                <button
                  type="button"
                  onClick={handlePrevMonth}
                  style={{
                    width: '24px',
                    height: '24px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: '#6b7280',
                    cursor: 'pointer',
                    border: 'none',
                    background: 'none',
                  }}
                >
                  <span style={{ fontSize: '18px', fontWeight: '300' }}>‹</span>
                </button>
                <h2
                  style={{
                    fontSize: '14px',
                    fontWeight: '700',
                    color: '#1f2937',
                    margin: '0 8px',
                    whiteSpace: 'nowrap',
                  }}
                >
                  {monthName}
                </h2>
                <button
                  type="button"
                  onClick={handleNextMonth}
                  style={{
                    width: '24px',
                    height: '24px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: '#6b7280',
                    cursor: 'pointer',
                    border: 'none',
                    background: 'none',
                  }}
                >
                  <span style={{ fontSize: '18px', fontWeight: '300' }}>›</span>
                </button>
                <button
                  type="button"
                  onClick={handleToday}
                  style={{
                    marginLeft: '8px',
                    padding: '2px 8px',
                    fontSize: '12px',
                    backgroundColor: '#dbeafe',
                    color: '#1e40af',
                    borderRadius: '4px',
                    border: 'none',
                    cursor: 'pointer',
                    fontWeight: '500',
                  }}
                >
                  今
                </button>
              </div>
              {renderCalendarGrid(days, true)}
            </div>

            {/* 筛选按钮 */}
            <div
              style={{
                backgroundColor: 'white',
                borderRadius: '12px',
                boxShadow: '0 1px 3px 0 rgba(0, 0, 0, 0.1)',
                padding: '12px',
              }}
            >
              <h3
                style={{
                  fontSize: '12px',
                  fontWeight: '600',
                  color: '#4b5563',
                  marginBottom: '8px',
                }}
              >
                我的日历
              </h3>
              <div
                style={{ display: 'flex', flexDirection: 'column', gap: '6px' }}
              >
                <label
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '8px',
                    cursor: 'pointer',
                  }}
                >
                  <input
                    type="checkbox"
                    checked={showMeetings}
                    onChange={(e) => setShowMeetings(e.target.checked)}
                    style={{ width: '16px', height: '16px' }}
                  />
                  <span style={{ fontSize: '14px', color: '#374151' }}>
                    会议信息
                  </span>
                </label>
                <label
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '8px',
                    cursor: 'pointer',
                  }}
                >
                  <input
                    type="checkbox"
                    checked={showTasks}
                    onChange={(e) => setShowTasks(e.target.checked)}
                    style={{ width: '16px', height: '16px' }}
                  />
                  <span style={{ fontSize: '14px', color: '#374151' }}>
                    待办任务
                  </span>
                </label>
                <label
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '8px',
                    cursor: 'pointer',
                  }}
                >
                  <input
                    type="checkbox"
                    checked={showWeekends}
                    onChange={(e) => setShowWeekends(e.target.checked)}
                    style={{ width: '16px', height: '16px' }}
                  />
                  <span style={{ fontSize: '14px', color: '#374151' }}>
                    显示周末
                  </span>
                </label>
              </div>
            </div>

            {/* 当日事件列表 */}
            <div
              style={{
                backgroundColor: 'white',
                borderRadius: '12px',
                boxShadow: '0 1px 3px 0 rgba(0, 0, 0, 0.1)',
                padding: '12px',
                maxHeight: '300px',
                overflowY: 'auto',
              }}
            >
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  marginBottom: '8px',
                }}
              >
                <h2
                  style={{
                    fontSize: '14px',
                    fontWeight: '700',
                    color: '#1f2937',
                  }}
                >
                  {selectedDate.toLocaleDateString('zh-CN', {
                    month: 'long',
                    day: 'numeric',
                  })}
                </h2>
                <button
                  type="button"
                  onClick={openCreateModal}
                  style={{
                    padding: '4px 8px',
                    backgroundColor: '#2563eb',
                    color: 'white',
                    borderRadius: '8px',
                    fontSize: '12px',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '4px',
                    border: 'none',
                    cursor: 'pointer',
                  }}
                >
                  <Plus size={12} /> 新建
                </button>
              </div>
              {getEventsForDate(selectedDate).length === 0 ? (
                <p
                  style={{
                    color: '#9ca3af',
                    fontSize: '12px',
                    textAlign: 'center',
                    padding: '16px 0',
                  }}
                >
                  暂无事件
                </p>
              ) : (
                <ul
                  style={{
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '8px',
                    listStyle: 'none',
                    padding: 0,
                    margin: 0,
                  }}
                >
                  {getEventsForDate(selectedDate).map((ev) => (
                    <li
                      key={ev.id}
                      style={{
                        borderLeft: `4px solid ${ev.color}`,
                        borderRadius: '8px',
                        padding: '8px',
                        backgroundColor: '#f9fafb',
                        cursor: 'pointer',
                      }}
                    >
                      <div
                        style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          alignItems: 'start',
                          marginBottom: '4px',
                        }}
                      >
                        <span
                          style={{
                            fontWeight: '600',
                            fontSize: '12px',
                            color: '#1f2937',
                            flex: 1,
                          }}
                        >
                          {ev.title}
                        </span>
                        <div style={{ display: 'flex', gap: '4px' }}>
                          <button
                            type="button"
                            onClick={(e) => {
                              e.stopPropagation();
                              openEditModal(ev);
                            }}
                            style={{
                              padding: '4px',
                              border: 'none',
                              background: 'none',
                              cursor: 'pointer',
                              display: 'flex',
                              alignItems: 'center',
                            }}
                          >
                            <Edit2 size={14} color="#4b5563" />
                          </button>
                          <button
                            type="button"
                            onClick={(e) => {
                              e.stopPropagation();
                              handleDelete(ev.id);
                            }}
                            style={{
                              padding: '4px',
                              border: 'none',
                              background: 'none',
                              cursor: 'pointer',
                              display: 'flex',
                              alignItems: 'center',
                            }}
                          >
                            <Trash2 size={14} color="#ef4444" />
                          </button>
                        </div>
                      </div>
                      <div
                        style={{
                          fontSize: '12px',
                          color: '#6b7280',
                          display: 'flex',
                          alignItems: 'center',
                          gap: '4px',
                        }}
                      >
                        <span>{formatTime(ev.start)}</span>
                        <span>-</span>
                        <span>{formatTime(ev.end)}</span>
                      </div>
                      {ev.description && (
                        <div
                          style={{
                            fontSize: '12px',
                            color: '#9ca3af',
                            marginTop: '4px',
                          }}
                        >
                          {ev.description}
                        </div>
                      )}
                    </li>
                  ))}
                </ul>
              )}
            </div>
          </div>

          {/* 右侧大日历 */}
          <div
            style={{
              flex: 1,
              minWidth: 0,
              backgroundColor: 'white',
              borderRadius: '12px',
              boxShadow: '0 1px 3px 0 rgba(0, 0, 0, 0.1)',
              padding: '24px',
            }}
          >
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                marginBottom: '16px',
                position: 'relative',
              }}
            >
              <div
                style={{ display: 'flex', alignItems: 'center', gap: '8px' }}
              >
                <button
                  type="button"
                  onClick={handlePrevMonth}
                  style={{
                    width: '32px',
                    height: '32px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: '#9ca3af',
                    cursor: 'pointer',
                    border: 'none',
                    background: 'none',
                    transition: 'color 0.2s',
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.color = '#4b5563';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.color = '#9ca3af';
                  }}
                >
                  <span style={{ fontSize: '24px', fontWeight: '300' }}>‹</span>
                </button>
                <h2
                  style={{
                    fontSize: '24px',
                    fontWeight: '700',
                    color: '#1f2937',
                    margin: 0,
                    whiteSpace: 'nowrap',
                  }}
                >
                  {monthName}
                </h2>
                <button
                  type="button"
                  onClick={handleNextMonth}
                  style={{
                    width: '32px',
                    height: '32px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: '#9ca3af',
                    cursor: 'pointer',
                    border: 'none',
                    background: 'none',
                    transition: 'color 0.2s',
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.color = '#4b5563';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.color = '#9ca3af';
                  }}
                >
                  <span style={{ fontSize: '24px', fontWeight: '300' }}>›</span>
                </button>
                <button
                  type="button"
                  onClick={handleToday}
                  style={{
                    padding: '6px 16px',
                    backgroundColor: '#2563eb',
                    color: 'white',
                    borderRadius: '8px',
                    border: 'none',
                    cursor: 'pointer',
                    fontWeight: '500',
                    fontSize: '14px',
                    whiteSpace: 'nowrap',
                    transition: 'background-color 0.2s',
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.backgroundColor = '#1d4ed8';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.backgroundColor = '#2563eb';
                  }}
                >
                  今天
                </button>
              </div>

              <div
                style={{
                  position: 'absolute',
                  right: 0,
                  display: 'flex',
                  backgroundColor: '#f3f4f6',
                  borderRadius: '8px',
                  padding: '4px',
                }}
              >
                <button
                  type="button"
                  onClick={() => setViewMode('month')}
                  style={{
                    padding: '6px 12px',
                    borderRadius: '6px',
                    fontSize: '14px',
                    fontWeight: '500',
                    border: 'none',
                    cursor: 'pointer',
                    whiteSpace: 'nowrap',
                    backgroundColor:
                      viewMode === 'month' ? '#ffffff' : 'transparent',
                    color: viewMode === 'month' ? '#111827' : '#6b7280',
                    boxShadow:
                      viewMode === 'month'
                        ? '0 1px 2px 0 rgba(0, 0, 0, 0.05)'
                        : 'none',
                    transition: 'all 0.2s',
                  }}
                >
                  月
                </button>
                <button
                  type="button"
                  onClick={() => setViewMode('day')}
                  style={{
                    padding: '6px 12px',
                    borderRadius: '6px',
                    fontSize: '14px',
                    fontWeight: '500',
                    border: 'none',
                    cursor: 'pointer',
                    whiteSpace: 'nowrap',
                    backgroundColor:
                      viewMode === 'day' ? '#ffffff' : 'transparent',
                    color: viewMode === 'day' ? '#111827' : '#6b7280',
                    boxShadow:
                      viewMode === 'day'
                        ? '0 1px 2px 0 rgba(0, 0, 0, 0.05)'
                        : 'none',
                    transition: 'all 0.2s',
                  }}
                >
                  天
                </button>
              </div>
            </div>
            {viewMode === 'month' ? (
              renderCalendarGrid(days, false)
            ) : (
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'column',
                  gap: '12px',
                }}
              >
                <h3
                  style={{
                    fontSize: '18px',
                    fontWeight: '600',
                    color: '#1f2937',
                  }}
                >
                  {selectedDate.toLocaleDateString('zh-CN', {
                    year: 'numeric',
                    month: 'long',
                    day: 'numeric',
                    weekday: 'long',
                  })}
                </h3>
                {getEventsForDate(selectedDate).length === 0 ? (
                  <p
                    style={{
                      color: '#9ca3af',
                      textAlign: 'center',
                      padding: '48px 0',
                    }}
                  >
                    暂无事件
                  </p>
                ) : (
                  <div
                    style={{
                      display: 'flex',
                      flexDirection: 'column',
                      gap: '8px',
                    }}
                  >
                    {getEventsForDate(selectedDate).map((ev) => (
                      <div
                        key={ev.id}
                        style={{
                          borderLeft: `4px solid ${ev.color}`,
                          borderRadius: '8px',
                          padding: '16px',
                          backgroundColor: '#f9fafb',
                          cursor: 'pointer',
                        }}
                        onClick={() => openEditModal(ev)}
                      >
                        <div
                          style={{
                            display: 'flex',
                            justifyContent: 'space-between',
                            alignItems: 'start',
                            marginBottom: '8px',
                          }}
                        >
                          <h4
                            style={{
                              fontWeight: '600',
                              fontSize: '16px',
                              color: '#1f2937',
                              margin: 0,
                            }}
                          >
                            {ev.title}
                          </h4>
                          <div style={{ display: 'flex', gap: '8px' }}>
                            <button
                              type="button"
                              onClick={(e) => {
                                e.stopPropagation();
                                openEditModal(ev);
                              }}
                              style={{
                                padding: '6px',
                                border: 'none',
                                background: 'none',
                                cursor: 'pointer',
                                display: 'flex',
                                alignItems: 'center',
                              }}
                            >
                              <Edit2 size={16} color="#4b5563" />
                            </button>
                            <button
                              type="button"
                              onClick={(e) => {
                                e.stopPropagation();
                                handleDelete(ev.id);
                              }}
                              style={{
                                padding: '6px',
                                border: 'none',
                                background: 'none',
                                cursor: 'pointer',
                                display: 'flex',
                                alignItems: 'center',
                              }}
                            >
                              <Trash2 size={16} color="#ef4444" />
                            </button>
                          </div>
                        </div>
                        <div
                          style={{
                            fontSize: '14px',
                            color: '#6b7280',
                            display: 'flex',
                            alignItems: 'center',
                            gap: '8px',
                            marginBottom: '8px',
                          }}
                        >
                          <span>{formatTime(ev.start)}</span>
                          <span>-</span>
                          <span>{formatTime(ev.end)}</span>
                          <span
                            style={{
                              padding: '2px 8px',
                              borderRadius: '4px',
                              fontSize: '12px',
                              backgroundColor: `${ev.color}20`,
                              color: ev.color,
                            }}
                          >
                            {eventTypes.find((t) => t.value === ev.type)?.label}
                          </span>
                        </div>
                        {ev.description && (
                          <p
                            style={{
                              fontSize: '14px',
                              color: '#6b7280',
                              margin: 0,
                            }}
                          >
                            {ev.description}
                          </p>
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Modal - 使用 portal 样式的居中布局 */}
      {showModal && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: 9999,
            padding: '16px',
          }}
          onClick={() => setShowModal(false)}
        >
          <div
            style={{
              backgroundColor: 'white',
              borderRadius: '12px',
              boxShadow:
                '0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)',
              maxWidth: '512px',
              width: '100%',
              maxHeight: '90vh',
              display: 'flex',
              flexDirection: 'column',
              overflow: 'hidden',
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <div style={{ padding: '24px', overflowY: 'auto', flex: 1 }}>
              <h2
                style={{
                  fontSize: '24px',
                  fontWeight: '700',
                  color: '#1f2937',
                  marginBottom: '24px',
                  margin: 0,
                }}
              >
                {editingEvent ? '编辑事件' : '新建事件'}
              </h2>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'column',
                  gap: '16px',
                }}
              >
                <div>
                  <label
                    htmlFor="event-title"
                    style={{
                      display: 'block',
                      fontSize: '14px',
                      fontWeight: '500',
                      color: '#374151',
                      marginBottom: '4px',
                    }}
                  >
                    事件标题
                  </label>
                  <input
                    type="text"
                    id="event-title"
                    value={formData.title}
                    onChange={(e) =>
                      setFormData({ ...formData, title: e.target.value })
                    }
                    placeholder="请输入事件标题"
                    style={{
                      width: '100%',
                      padding: '8px 16px',
                      border: '1px solid #d1d5db',
                      borderRadius: '8px',
                      fontSize: '14px',
                      outline: 'none',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>
                <div
                  style={{
                    display: 'grid',
                    gridTemplateColumns: '1fr 1fr',
                    gap: '16px',
                  }}
                >
                  <div>
                    <label
                      htmlFor="event-start-date"
                      style={{
                        display: 'block',
                        fontSize: '14px',
                        fontWeight: '500',
                        color: '#374151',
                        marginBottom: '4px',
                      }}
                    >
                      开始日期
                    </label>
                    <input
                      type="date"
                      id="event-start-date"
                      value={formData.startDate}
                      onChange={(e) =>
                        setFormData({ ...formData, startDate: e.target.value })
                      }
                      style={{
                        width: '100%',
                        padding: '8px 16px',
                        border: '1px solid #d1d5db',
                        borderRadius: '8px',
                        fontSize: '14px',
                        outline: 'none',
                        boxSizing: 'border-box',
                      }}
                    />
                  </div>
                  <div>
                    <label
                      htmlFor="event-start-time"
                      style={{
                        display: 'block',
                        fontSize: '14px',
                        fontWeight: '500',
                        color: '#374151',
                        marginBottom: '4px',
                      }}
                    >
                      开始时间
                    </label>
                    <input
                      type="time"
                      id="event-start-time"
                      value={formData.startTime}
                      onChange={(e) =>
                        setFormData({ ...formData, startTime: e.target.value })
                      }
                      style={{
                        width: '100%',
                        padding: '8px 16px',
                        border: '1px solid #d1d5db',
                        borderRadius: '8px',
                        fontSize: '14px',
                        outline: 'none',
                        boxSizing: 'border-box',
                      }}
                    />
                  </div>
                </div>
                <div
                  style={{
                    display: 'grid',
                    gridTemplateColumns: '1fr 1fr',
                    gap: '16px',
                  }}
                >
                  <div>
                    <label
                      htmlFor="event-end-date"
                      style={{
                        display: 'block',
                        fontSize: '14px',
                        fontWeight: '500',
                        color: '#374151',
                        marginBottom: '4px',
                      }}
                    >
                      结束日期
                    </label>
                    <input
                      type="date"
                      id="event-end-date"
                      value={formData.endDate}
                      onChange={(e) =>
                        setFormData({ ...formData, endDate: e.target.value })
                      }
                      style={{
                        width: '100%',
                        padding: '8px 16px',
                        border: '1px solid #d1d5db',
                        borderRadius: '8px',
                        fontSize: '14px',
                        outline: 'none',
                        boxSizing: 'border-box',
                      }}
                    />
                  </div>
                  <div>
                    <label
                      htmlFor="event-end-time"
                      style={{
                        display: 'block',
                        fontSize: '14px',
                        fontWeight: '500',
                        color: '#374151',
                        marginBottom: '4px',
                      }}
                    >
                      结束时间
                    </label>
                    <input
                      type="time"
                      id="event-end-time"
                      value={formData.endTime}
                      onChange={(e) =>
                        setFormData({ ...formData, endTime: e.target.value })
                      }
                      style={{
                        width: '100%',
                        padding: '8px 16px',
                        border: '1px solid #d1d5db',
                        borderRadius: '8px',
                        fontSize: '14px',
                        outline: 'none',
                        boxSizing: 'border-box',
                      }}
                    />
                  </div>
                </div>
                <div>
                  <label
                    htmlFor="event-type"
                    style={{
                      display: 'block',
                      fontSize: '14px',
                      fontWeight: '500',
                      color: '#374151',
                      marginBottom: '4px',
                    }}
                  >
                    事件类型
                  </label>
                  <select
                    id="event-type"
                    value={formData.type}
                    onChange={(e) =>
                      setFormData({
                        ...formData,
                        type: e.target.value as CalendarEvent['type'],
                      })
                    }
                    style={{
                      width: '100%',
                      padding: '8px 16px',
                      border: '1px solid #d1d5db',
                      borderRadius: '8px',
                      fontSize: '14px',
                      outline: 'none',
                      boxSizing: 'border-box',
                    }}
                  >
                    {eventTypes.map((t) => (
                      <option key={t.value} value={t.value}>
                        {t.label}
                      </option>
                    ))}
                  </select>
                </div>
                <div>
                  <label
                    htmlFor="event-description"
                    style={{
                      display: 'block',
                      fontSize: '14px',
                      fontWeight: '500',
                      color: '#374151',
                      marginBottom: '4px',
                    }}
                  >
                    描述
                  </label>
                  <textarea
                    id="event-description"
                    value={formData.description}
                    onChange={(e) =>
                      setFormData({ ...formData, description: e.target.value })
                    }
                    rows={3}
                    placeholder="请输入事件描述（可选）"
                    style={{
                      width: '100%',
                      padding: '8px 16px',
                      border: '1px solid #d1d5db',
                      borderRadius: '8px',
                      fontSize: '14px',
                      outline: 'none',
                      resize: 'none',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>
              </div>
              <div style={{ display: 'flex', gap: '12px', marginTop: '24px' }}>
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  style={{
                    flex: 1,
                    padding: '8px 16px',
                    backgroundColor: '#f3f4f6',
                    color: '#374151',
                    borderRadius: '8px',
                    border: 'none',
                    cursor: 'pointer',
                    fontWeight: '500',
                    fontSize: '14px',
                  }}
                >
                  取消
                </button>
                <button
                  type="button"
                  onClick={handleSave}
                  style={{
                    flex: 1,
                    padding: '8px 16px',
                    backgroundColor: '#2563eb',
                    color: 'white',
                    borderRadius: '8px',
                    border: 'none',
                    cursor: 'pointer',
                    fontWeight: '500',
                    fontSize: '14px',
                  }}
                >
                  保存
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default CalendarManagement;
